/**
 * NotebookPageWrapper - Entry point for NotebookPage with URL state and data fetching
 *
 * This wrapper:
 * 1. Reads caseId from URL params
 * 2. Manages URL state via useNotebookURL
 * 3. Fetches data via useNotebookData
 * 4. Renders NotebookPage with all necessary props
 *
 * Route: /cases/:caseId (with optional ?tab=&layer= query params)
 */

import React, { useCallback, useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { toast } from 'sonner';
import { Loader2, X } from 'lucide-react';

import { NotebookPage } from '../components/notebook/NotebookPage';
import { useNotebookURL, NotebookTab, NotebookLayer } from '../hooks/useNotebookURL';
import { useNotebookData } from '../hooks/useNotebookData';
import { useCaseStore } from '../store/useCaseStore';
import { apiPost, apiGet } from '../utils/api';
import ErrorDisplay from '../components/ErrorDisplay';
import { useQuery, useQueryClient } from '@tanstack/react-query';
import FileManager from '../components/FileManager';
import { RepresentationBanner } from '../components/representation/RepresentationBanner';

// Skeleton loader for NotebookPage
function NotebookSkeleton() {
  return (
    <div className="h-full flex flex-col bg-slate-950">
      {/* Header skeleton */}
      <div className="border-b border-slate-800 p-4">
        <div className="animate-pulse flex items-center gap-4">
          <div className="h-8 w-48 bg-slate-800 rounded" />
          <div className="h-6 w-24 bg-slate-800 rounded" />
          <div className="flex-1" />
          <div className="h-8 w-32 bg-slate-800 rounded" />
        </div>
      </div>

      {/* Content skeleton */}
      <div className="flex-1 flex overflow-hidden">
        {/* Canvas skeleton */}
        <div className="flex-1 p-6">
          <div className="animate-pulse space-y-4">
            <div className="h-32 bg-slate-800/50 rounded-xl" />
            <div className="h-48 bg-slate-800/50 rounded-xl" />
            <div className="h-24 bg-slate-800/50 rounded-xl" />
          </div>
        </div>

        {/* Rail skeleton */}
        <div className="w-72 border-r border-slate-800 p-4">
          <div className="animate-pulse space-y-3">
            {[...Array(8)].map((_, i) => (
              <div key={i} className="h-10 bg-slate-800 rounded" />
            ))}
          </div>
        </div>
      </div>

      {/* Input skeleton */}
      <div className="border-t border-slate-800 p-4">
        <div className="animate-pulse">
          <div className="h-12 bg-slate-800 rounded-xl max-w-4xl mx-auto" />
        </div>
      </div>
    </div>
  );
}

// Error display component
function NotebookError({ error, onRetry }: { error: Error; onRetry?: () => void }) {
  return (
    <div className="h-full flex items-center justify-center bg-slate-950">
      <ErrorDisplay
        error={error}
        onRetry={onRetry}
        className="max-w-md"
      />
    </div>
  );
}

export function NotebookPageWrapper() {
  const { caseId } = useParams<{ caseId: string }>();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const { state: urlState, setTab, setLayer, setSessionId, setArtifactId } = useNotebookURL();

  // Upload modal state
  const [showUploadModal, setShowUploadModal] = useState(false);

  // Track if representation is confirmed for showing banner
  const [representationConfirmed, setRepresentationConfirmed] = useState(false);

  // Sync case ID with global store
  const { setActiveCase } = useCaseStore();

  // First, fetch case data
  const {
    data: caseData,
    isLoading: caseLoading,
    isError: caseError,
    error: caseErrorData,
    refetch: refetchCase,
  } = useQuery({
    queryKey: ['case', caseId],
    queryFn: () => apiGet(`/api/cases/${caseId}`),
    enabled: !!caseId,
    staleTime: 30_000,
  });

  // Then, use notebook data hook with the case data
  const {
    v1Data,
    v2Data,
    v3Data,
    sources,
    capabilities,
    isLoading: notebookLoading,
    error: notebookError,
    runCapability,
    refreshData,
  } = useNotebookData({
    caseId: caseId || '',
    caseName: caseData?.name || caseData?.case_name,
    analysisResult: caseData,
    documents: caseData?.files_data || caseData?.files,
  });

  // Sync case to global store
  useEffect(() => {
    if (caseData && caseId) {
      setActiveCase(caseId, caseData.name || caseData.case_name || null);
    }
  }, [caseData, caseId, setActiveCase]);

  // Redirect to dashboard if no caseId
  useEffect(() => {
    if (!caseId) {
      navigate('/cases');
    }
  }, [caseId, navigate]);

  // Handle running a capability
  const handleRunCapability = useCallback(async (capabilityId: string) => {
    if (!caseId) return;

    try {
      const result = await runCapability(capabilityId);

      // Check if result is an error object
      if (result && typeof result === 'object' && 'error' in result) {
        const errorResult = result as { error: boolean; code: string; message: string; status: number };
        if (errorResult.error) {
          // Handle 428 (representation not confirmed) specially
          if (errorResult.status === 428) {
            toast.error(errorResult.message || 'יש לאשר את צד הייצוג לפני הפעלת ניתוח זה');
            // Show the representation banner
            setRepresentationConfirmed(false);
          } else {
            toast.error(errorResult.message || 'שגיאה בהרצת הניתוח');
          }
          return null;
        }
      }

      // Ensure NotebookLM reflects new results immediately
      await refreshData();
      toast.success('הניתוח הושלם בהצלחה');
      return result;
    } catch (err) {
      toast.error('שגיאה בהרצת הניתוח');
      throw err;
    }
  }, [caseId, runCapability, refreshData]);

  // Handle document open - navigate to document viewer
  const handleDocumentOpen = useCallback((sourceId: string, page?: number, quote?: string) => {
    if (!caseId) return;

    // Build URL with optional query params
    let url = `/cases/${caseId}/documents/${sourceId}`;
    const params = new URLSearchParams();
    if (page) params.set('page', String(page));
    if (quote) params.set('quote', quote);

    const queryString = params.toString();
    if (queryString) {
      url += `?${queryString}`;
    }

    navigate(url);
  }, [caseId, navigate]);

  // Handle send message
  const handleSendMessage = useCallback(async (message: string) => {
    if (!caseId) return;

    try {
      await apiPost(`/api/cases/${caseId}/chat`, {
        message,
        session_id: urlState.sessionId,
      });
      await refreshData();
    } catch (err) {
      toast.error('שגיאה בשליחת ההודעה');
      throw err;
    }
  }, [caseId, urlState.sessionId, refreshData]);

  // Handle upload trigger - show modal instead of redirecting to legacy page
  const handleUpload = useCallback(() => {
    setShowUploadModal(true);
  }, []);

  // Handle file upload completion
  const handleFilesUpdate = useCallback(async (result: unknown) => {
    setShowUploadModal(false);
    // Invalidate case query to refresh data
    queryClient.invalidateQueries({ queryKey: ['case', caseId] });
    // Refresh notebook data
    await refreshData();
    toast.success('הקבצים הועלו בהצלחה');
  }, [caseId, queryClient, refreshData]);

  // Handle representation confirmation - must be before early returns
  const handleRepresentationConfirmed = useCallback((side: string) => {
    setRepresentationConfirmed(true);
    // Refresh capabilities after representation is confirmed
    refreshData();
    toast.success('צד הייצוג אושר - כעת ניתן להפעיל את כל הניתוחים');
  }, [refreshData]);

  // Loading state
  const isLoading = caseLoading || notebookLoading;
  if (isLoading) {
    return <NotebookSkeleton />;
  }

  // Error state
  if (caseError || notebookError) {
    const error = caseErrorData || new Error(notebookError || 'Failed to load case');
    return (
      <NotebookError
        error={error instanceof Error ? error : new Error(String(error))}
        onRetry={refetchCase}
      />
    );
  }

  // No case data
  if (!caseData || !caseId) {
    return (
      <NotebookError
        error={new Error('Case not found')}
        onRetry={() => navigate('/cases')}
      />
    );
  }

  return (
    <>
      {/* Show RepresentationBanner if not yet confirmed */}
      {!representationConfirmed && caseId && (
        <div className="fixed top-0 left-0 right-0 z-40">
          <RepresentationBanner
            caseId={caseId}
            onConfirmed={handleRepresentationConfirmed}
          />
        </div>
      )}

      <NotebookPage
        caseId={caseId}
        caseName={caseData.name || caseData.case_name || 'תיק ללא שם'}
        initialData={{
          v1: v1Data,
          v2: v2Data,
          sources: sources,
          capabilities: capabilities,
        }}
        // Convert notebook contradictions to workbench format
        contradictions={(v2Data?.contradictions || []).map((c: any) => ({
          id: c.id,
          claim_a: c.claim1?.short_statement || c.claim1?.text || c.description || '',
          claim_b: c.claim2?.short_statement || c.claim2?.text || '',
          severity: c.severity || 'medium',
          type: c.type || 'general',
          sources: [
            ...(c.evidence || []).map((e: any) => e.file_id).filter(Boolean),
            c.claim1?.evidence?.[0]?.file_id,
            c.claim2?.evidence?.[0]?.file_id,
          ].filter(Boolean) as string[],
          explanation: c.description_he || c.description || '',
        }))}
        onFetchV1={async () => {
          return v1Data || {
            summary: { text: '' },
            keyPoints: [],
            actors: [],
          };
        }}
        onFetchV2={async () => {
          return v2Data || {
            claims: [],
            actors: [],
            timeline: [],
            contradictions: [],
            redFlags: [],
          };
        }}
        onRunCapability={handleRunCapability}
        onDocumentOpen={handleDocumentOpen}
        onSendMessage={handleSendMessage}
        onUpload={handleUpload}
      />

      {/* Upload Modal - stays in NotebookPage instead of redirecting to legacy */}
      {showUploadModal && caseData && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50 flex items-center justify-center p-4">
          <div className="bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900 rounded-2xl shadow-2xl w-full max-w-4xl max-h-[90vh] overflow-hidden flex flex-col border border-white/20">
            <div className="bg-gradient-to-r from-purple-600 to-blue-600 p-6 text-white flex items-center justify-between">
              <div>
                <h2 className="text-2xl font-bold">העלאת קבצים לתיק: {caseData.name || caseData.case_name || 'תיק ללא שם'}</h2>
                <p className="text-purple-100 text-sm mt-1">הוסף מסמכים והרץ ניתוח מחדש</p>
              </div>
              <button
                onClick={() => setShowUploadModal(false)}
                className="p-2 rounded-full hover:bg-white/10 transition-colors"
              >
                <X className="w-6 h-6" />
              </button>
            </div>
            <div className="flex-1 overflow-y-auto p-6">
              <FileManager
                caseId={caseId}
                caseName={caseData.name || caseData.case_name || ''}
                files={caseData.files_data || caseData.files || []}
                onFilesUpdate={handleFilesUpdate}
              />
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default NotebookPageWrapper;
