/**
 * RepresentationBanner - Auto-detects representation side (C9)
 *
 * Non-blocking banner that:
 * - Triggers auto-detection on mount
 * - Polls for status until done/failed/timeout
 * - Shows detected side with confidence
 * - Allows user to confirm or override
 */

import React, { useState, useEffect, useCallback, useRef } from 'react';
import {
  Users, Loader2, Check, X, AlertTriangle, Edit2, ChevronDown
} from 'lucide-react';
import { toast } from 'sonner';
import {
  representationAutoTrigger,
  representationStatus,
  representationConfirm,
  RepresentationStatusResponse
} from '../../api/legalNotebook';

// =============================================================================
// Types
// =============================================================================

interface RepresentationBannerProps {
  caseId: string;
  onConfirmed?: (side: string) => void;
}

type RepresentationSide = 'plaintiff' | 'defendant' | 'petitioner' | 'respondent' | 'appellant' | 'other' | 'unknown';
type ConfirmableSide = Exclude<RepresentationSide, 'unknown'>;

const SIDE_LABELS: Record<string, string> = {
  plaintiff: 'תובע',
  defendant: 'נתבע',
  petitioner: 'מבקש',
  respondent: 'משיב',
  appellant: 'מערער',
  respondent_appeal: 'משיב בערעור',
  respondent_admin: 'משיב (מנהלי)',
  other: 'אחר',
  unknown: 'לא ידוע',
};

const CONFIDENCE_LABELS: Record<string, { label: string; color: string }> = {
  high: { label: 'גבוהה', color: 'text-green-600' },
  medium: { label: 'בינונית', color: 'text-yellow-600' },
  low: { label: 'נמוכה', color: 'text-red-600' },
};

// =============================================================================
// Component
// =============================================================================

export const RepresentationBanner: React.FC<RepresentationBannerProps> = ({
  caseId,
  onConfirmed
}) => {
  // State
  const [status, setStatus] = useState<'idle' | 'running' | 'done' | 'failed' | 'timeout'>('idle');
  const [jobId, setJobId] = useState<string | null>(null);
  const [detectedSide, setDetectedSide] = useState<string | null>(null);
  const [confidence, setConfidence] = useState<number>(0);
  const [confirmedSide, setConfirmedSide] = useState<string | null>(null);
  const [showEditModal, setShowEditModal] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Refs for polling
  const pollCount = useRef(0);
  const pollInterval = useRef<NodeJS.Timeout | null>(null);

  const MAX_POLLS = 15; // 30 seconds (2s interval)
  const POLL_INTERVAL_MS = 2000;

  // =============================================================================
  // Polling Logic
  // =============================================================================

  const stopPolling = useCallback(() => {
    if (pollInterval.current) {
      clearInterval(pollInterval.current);
      pollInterval.current = null;
    }
  }, []);

  const pollStatus = useCallback(async () => {
    if (!jobId) return;

    pollCount.current += 1;

    // Check timeout
    if (pollCount.current >= MAX_POLLS) {
      stopPolling();
      setStatus('timeout');
      setError('זמן הזיהוי פג - נסה שוב');
      return;
    }

    try {
      const result: RepresentationStatusResponse = await representationStatus(caseId, jobId);

      if (result.status === 'completed') {
        stopPolling();
        setStatus('done');
        if (result.representation) {
          setDetectedSide(result.representation.side_confirmed);
          setConfidence(result.representation.confidence);
        }
      } else if (result.status === 'failed') {
        stopPolling();
        setStatus('failed');
        setError(result.error || 'הזיהוי נכשל');
      }
      // If still running/pending, keep polling
    } catch (err: any) {
      console.error('Poll status error:', err);
      // Don't stop polling on transient errors
    }
  }, [caseId, jobId, stopPolling]);

  // Start polling when jobId is set
  useEffect(() => {
    if (jobId && status === 'running') {
      pollCount.current = 0;
      pollInterval.current = setInterval(pollStatus, POLL_INTERVAL_MS);
    }

    return () => stopPolling();
  }, [jobId, status, pollStatus, stopPolling]);

  // =============================================================================
  // Trigger Detection
  // =============================================================================

  const triggerDetection = useCallback(async () => {
    setStatus('running');
    setError(null);
    setDetectedSide(null);

    try {
      const result = await representationAutoTrigger(caseId);
      setJobId(result.job_id);

      // If already completed (sync response)
      if (result.status === 'completed') {
        setStatus('done');
      }
    } catch (err: any) {
      console.error('Trigger detection error:', err);
      setStatus('failed');
      setError(err.message || 'שגיאה בזיהוי');
    }
  }, [caseId]);

  // Auto-trigger on mount
  useEffect(() => {
    triggerDetection();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [caseId]);

  // =============================================================================
  // Confirm/Override
  // =============================================================================

  const handleConfirm = useCallback(async () => {
    if (!detectedSide) return;

    try {
      await representationConfirm(caseId, { side: detectedSide as any });
      setConfirmedSide(detectedSide);
      toast.success('צד הייצוג אושר');
      onConfirmed?.(detectedSide);
    } catch (err: any) {
      console.error('Confirm error:', err);
      toast.error(err.message || 'שגיאה באישור');
    }
  }, [caseId, detectedSide, onConfirmed]);

  const handleOverride = useCallback(async (newSide: ConfirmableSide) => {
    try {
      await representationConfirm(caseId, { side: newSide });
      setConfirmedSide(newSide);
      setDetectedSide(newSide);
      setShowEditModal(false);
      toast.success('צד הייצוג עודכן');
      onConfirmed?.(newSide);
    } catch (err: any) {
      console.error('Override error:', err);
      toast.error(err.message || 'שגיאה בעדכון');
    }
  }, [caseId, onConfirmed]);

  // =============================================================================
  // Confidence Label
  // =============================================================================

  const getConfidenceLabel = (conf: number): { label: string; color: string } => {
    if (conf >= 0.85) return CONFIDENCE_LABELS.high;
    if (conf >= 0.65) return CONFIDENCE_LABELS.medium;
    return CONFIDENCE_LABELS.low;
  };

  // =============================================================================
  // Render
  // =============================================================================

  // Don't show if already confirmed
  if (confirmedSide) {
    return (
      <div className="mb-4 p-3 bg-green-50 border border-green-200 rounded-lg flex items-center justify-between" dir="rtl">
        <div className="flex items-center gap-3">
          <Check className="h-5 w-5 text-green-600" />
          <span className="text-green-800">
            מייצג את <strong>{SIDE_LABELS[confirmedSide] || confirmedSide}</strong>
          </span>
        </div>
        <button
          onClick={() => setShowEditModal(true)}
          className="text-sm text-green-600 hover:text-green-800 flex items-center gap-1"
        >
          <Edit2 className="h-4 w-4" />
          שנה
        </button>

        {/* Edit Modal */}
        {showEditModal && (
          <EditSideModal
            currentSide={confirmedSide}
            onSelect={handleOverride}
            onClose={() => setShowEditModal(false)}
          />
        )}
      </div>
    );
  }

  // Running state
  if (status === 'running') {
    return (
      <div className="mb-4 p-3 bg-blue-50 border border-blue-200 rounded-lg flex items-center gap-3" dir="rtl">
        <Loader2 className="h-5 w-5 text-blue-600 animate-spin" />
        <span className="text-blue-800">מזהה צד ייצוג...</span>
      </div>
    );
  }

  // Failed/Timeout state
  if (status === 'failed' || status === 'timeout') {
    return (
      <div className="mb-4 p-3 bg-yellow-50 border border-yellow-200 rounded-lg flex items-center justify-between" dir="rtl">
        <div className="flex items-center gap-3">
          <AlertTriangle className="h-5 w-5 text-yellow-600" />
          <span className="text-yellow-800">{error || 'לא ניתן לזהות צד ייצוג'}</span>
        </div>
        <div className="flex items-center gap-2">
          <button
            onClick={triggerDetection}
            className="text-sm px-3 py-1 text-yellow-700 border border-yellow-400 rounded hover:bg-yellow-100"
          >
            נסה שוב
          </button>
          <button
            onClick={() => setShowEditModal(true)}
            className="text-sm px-3 py-1 bg-yellow-600 text-white rounded hover:bg-yellow-700"
          >
            הגדר ידנית
          </button>
        </div>

        {showEditModal && (
          <EditSideModal
            currentSide={null}
            onSelect={handleOverride}
            onClose={() => setShowEditModal(false)}
          />
        )}
      </div>
    );
  }

  // Done state - show detected side
  if (status === 'done' && detectedSide) {
    const confLabel = getConfidenceLabel(confidence);

    return (
      <div className="mb-4 p-3 bg-blue-50 border border-blue-200 rounded-lg flex items-center justify-between" dir="rtl">
        <div className="flex items-center gap-3">
          <Users className="h-5 w-5 text-blue-600" />
          <span className="text-blue-800">
            זיהוי: מייצג את <strong>{SIDE_LABELS[detectedSide] || detectedSide}</strong>
            <span className={`mr-2 text-sm ${confLabel.color}`}>
              (ודאות {confLabel.label})
            </span>
          </span>
        </div>
        <div className="flex items-center gap-2">
          <button
            onClick={handleConfirm}
            className="text-sm px-3 py-1 bg-blue-600 text-white rounded hover:bg-blue-700 flex items-center gap-1"
          >
            <Check className="h-4 w-4" />
            אשר
          </button>
          <button
            onClick={() => setShowEditModal(true)}
            className="text-sm px-3 py-1 text-blue-600 border border-blue-400 rounded hover:bg-blue-100 flex items-center gap-1"
          >
            <Edit2 className="h-4 w-4" />
            תקן
          </button>
        </div>

        {showEditModal && (
          <EditSideModal
            currentSide={detectedSide}
            onSelect={handleOverride}
            onClose={() => setShowEditModal(false)}
          />
        )}
      </div>
    );
  }

  // Idle/unknown - don't show anything
  return null;
};

// =============================================================================
// Edit Modal
// =============================================================================

interface EditSideModalProps {
  currentSide: string | null;
  onSelect: (side: ConfirmableSide) => void;
  onClose: () => void;
}

const EditSideModal: React.FC<EditSideModalProps> = ({ currentSide, onSelect, onClose }) => {
  const sides: ConfirmableSide[] = ['plaintiff', 'defendant', 'petitioner', 'respondent', 'appellant', 'other'];

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50" onClick={onClose}>
      <div
        className="bg-white rounded-lg shadow-xl p-6 w-96 max-w-full"
        onClick={(e) => e.stopPropagation()}
        dir="rtl"
      >
        <h3 className="text-lg font-semibold mb-4">בחר צד ייצוג</h3>
        <div className="space-y-2">
          {sides.map((side) => (
            <button
              key={side}
              onClick={() => onSelect(side)}
              className={`w-full p-3 text-right rounded-lg border transition-colors ${
                currentSide === side
                  ? 'border-blue-500 bg-blue-50 text-blue-700'
                  : 'border-gray-200 hover:border-blue-300 hover:bg-gray-50'
              }`}
            >
              {SIDE_LABELS[side]}
            </button>
          ))}
        </div>
        <button
          onClick={onClose}
          className="mt-4 w-full py-2 text-gray-600 hover:text-gray-800"
        >
          ביטול
        </button>
      </div>
    </div>
  );
};

export default RepresentationBanner;
