/**
 * Legal Notebook API Client
 *
 * Typed API functions for Legal Notebook features (C7-C9):
 * - Precedent Dashboard & Query
 * - Contradiction Predicates & Context
 * - Representation Auto-Trigger & Status
 */

import { apiGet, apiPost } from '../utils/api';
import { LEGAL_NOTEBOOK_ROUTES } from './routes';

// =============================================================================
// Types
// =============================================================================

// Precedent Dashboard
export interface PrecedentDashboardFilters {
  court_type?: string;
  district?: string;
  years_from?: number;
  years_to?: number;
  procedural_posture?: string;
}

export interface RepresentativeCase {
  case_id: string;
  court: string;
  year: number;
  outcome: 'supporting' | 'against' | 'neutral';
  citation?: string;
  summary?: string;
}

export interface PrecedentDashboardResponse {
  claim_type: string;
  support_level: 'LOW' | 'MED' | 'HIGH';
  total_cases: number;
  time_window: string;
  representative_cases: {
    supporting: RepresentativeCase[];
    against: RepresentativeCase[];
  };
  filters_applied: PrecedentDashboardFilters;
}

// Precedent Query
export interface PrecedentQueryPayload {
  query: string;
  claim_type?: string;
  court_type?: string;
  district?: string;
  years_from?: number;
  years_to?: number;
  procedural_posture?: string;
  limit?: number;
}

export interface PrecedentCitation {
  quote: string;
  reference: string;
  page?: number;
  doc_id?: string;
}

export interface PrecedentQueryResult {
  case_id: string;
  title: string;
  court: string;
  year: number;
  why_relevant: string[];
  citations: PrecedentCitation[];
  outcome?: string;
}

export interface PrecedentQueryResponse {
  query: string;
  results: PrecedentQueryResult[];
  total_count: number;
  filters_applied: Record<string, any>;
}

// Contradiction Predicates
export interface ContradictionPredicatesPayload {
  contradiction_type?: string;
  text_block_a: string;
  text_block_b: string;
  context?: string;
}

export interface MappedPredicate {
  predicate_id: string;
  predicate_name: string;
  category: string;
  confidence: number;
}

export interface ContradictionPredicatesResponse {
  predicates: MappedPredicate[];
  contradiction_type?: string;
}

// Contradiction Precedent Context
export interface PrecedentContextPayload {
  mapped_predicates: string[];
  claim_type?: string;
  court_type?: string;
  district?: string;
  years_from?: number;
  years_to?: number;
  procedural_posture?: string;
}

export interface PrecedentContextResult {
  case_id: string;
  title: string;
  court: string;
  year: number;
  outcome?: string;
  citations: PrecedentCitation[];
  reasoning_factors?: string[];
}

export interface PrecedentContextResponse {
  predicates: string[];
  results: PrecedentContextResult[];
  total_count: number;
}

// Representation
export interface RepresentationTriggerResponse {
  job_id: string;
  status: 'pending' | 'running' | 'completed' | 'failed';
  case_id: string;
}

export interface RepresentationStatusResponse {
  job_id: string;
  status: 'pending' | 'running' | 'completed' | 'failed';
  case_id: string;
  representation?: {
    side_confirmed: string;
    confidence: number;
    evidence_refs?: Array<{
      signal: string;
      doc_id: string;
      quote?: string;
    }>;
  };
  error?: string;
}

export interface RepresentationConfirmPayload {
  side: 'plaintiff' | 'defendant' | 'petitioner' | 'respondent' | 'appellant' | 'other';
}

// =============================================================================
// API Functions
// =============================================================================

/**
 * Get precedent dashboard for a claim type
 */
export async function precedentDashboard(
  claimType: string,
  filters?: PrecedentDashboardFilters
): Promise<PrecedentDashboardResponse> {
  const url = LEGAL_NOTEBOOK_ROUTES.precedentDashboard(claimType);
  const params = new URLSearchParams();

  if (filters) {
    if (filters.court_type) params.append('court_type', filters.court_type);
    if (filters.district) params.append('district', filters.district);
    if (filters.years_from) params.append('years_from', String(filters.years_from));
    if (filters.years_to) params.append('years_to', String(filters.years_to));
    if (filters.procedural_posture) params.append('procedural_posture', filters.procedural_posture);
  }

  const queryString = params.toString();
  return apiGet<PrecedentDashboardResponse>(queryString ? `${url}?${queryString}` : url);
}

/**
 * Query precedents with free text and filters
 */
export async function precedentQuery(
  payload: PrecedentQueryPayload
): Promise<PrecedentQueryResponse> {
  return apiPost<PrecedentQueryResponse>(LEGAL_NOTEBOOK_ROUTES.precedentQuery, payload);
}

/**
 * Map contradiction to predicates
 */
export async function contradictionPredicates(
  payload: ContradictionPredicatesPayload
): Promise<ContradictionPredicatesResponse> {
  return apiPost<ContradictionPredicatesResponse>(
    LEGAL_NOTEBOOK_ROUTES.contradictionPredicates,
    payload
  );
}

/**
 * Get precedent context for contradiction
 */
export async function contradictionPrecedentContext(
  payload: PrecedentContextPayload
): Promise<PrecedentContextResponse> {
  return apiPost<PrecedentContextResponse>(
    LEGAL_NOTEBOOK_ROUTES.contradictionPrecedentContext,
    payload
  );
}

/**
 * Trigger auto representation detection
 */
export async function representationAutoTrigger(
  caseId: string
): Promise<RepresentationTriggerResponse> {
  return apiPost<RepresentationTriggerResponse>(
    LEGAL_NOTEBOOK_ROUTES.representationAutoTrigger(caseId),
    {}
  );
}

/**
 * Get representation job status
 */
export async function representationStatus(
  caseId: string,
  jobId: string
): Promise<RepresentationStatusResponse> {
  return apiGet<RepresentationStatusResponse>(
    LEGAL_NOTEBOOK_ROUTES.representationStatus(caseId, jobId)
  );
}

/**
 * Confirm/override representation side
 */
export async function representationConfirm(
  caseId: string,
  payload: RepresentationConfirmPayload
): Promise<{ success: boolean; side: string }> {
  return apiPost(LEGAL_NOTEBOOK_ROUTES.representationConfirm(caseId), payload);
}
