/**
 * useNotebookURL - URL state management for NotebookPage
 *
 * Manages tab and layer state via URL query parameters:
 * /cases/123?tab=claims&layer=v2&id=artifact-id&session=session-id
 */

import { useCallback, useMemo } from 'react';
import { useSearchParams } from 'react-router-dom';

export type NotebookTab =
  | 'chat'
  | 'summary'
  | 'claims'
  | 'contradictions'
  | 'timeline'
  | 'red_flags'
  | 'crosstabs'
  | 'game_theory'
  | 'debate'
  | 'precedents'
  | 'playbook';

export type NotebookLayer = 'v1' | 'v2' | 'v3';

export interface NotebookURLState {
  tab: NotebookTab;
  layer: NotebookLayer;
  artifactId?: string;
  sessionId?: string;
}

interface UseNotebookURLReturn {
  state: NotebookURLState;
  setTab: (tab: NotebookTab) => void;
  setLayer: (layer: NotebookLayer) => void;
  setArtifactId: (id: string | undefined) => void;
  setSessionId: (id: string | undefined) => void;
  setState: (newState: Partial<NotebookURLState>) => void;
  getDeepLink: (overrides?: Partial<NotebookURLState>) => string;
}

const DEFAULT_TAB: NotebookTab = 'chat';
const DEFAULT_LAYER: NotebookLayer = 'v1';

const VALID_TABS: NotebookTab[] = [
  'chat', 'summary', 'claims', 'contradictions', 'timeline',
  'red_flags', 'crosstabs', 'game_theory', 'debate', 'precedents', 'playbook'
];

const VALID_LAYERS: NotebookLayer[] = ['v1', 'v2', 'v3'];

function isValidTab(value: string | null): value is NotebookTab {
  return value !== null && VALID_TABS.includes(value as NotebookTab);
}

function isValidLayer(value: string | null): value is NotebookLayer {
  return value !== null && VALID_LAYERS.includes(value as NotebookLayer);
}

export function useNotebookURL(): UseNotebookURLReturn {
  const [searchParams, setSearchParams] = useSearchParams();

  // Parse current state from URL
  const state = useMemo<NotebookURLState>(() => {
    const tabParam = searchParams.get('tab');
    const layerParam = searchParams.get('layer');

    return {
      tab: isValidTab(tabParam) ? tabParam : DEFAULT_TAB,
      layer: isValidLayer(layerParam) ? layerParam : DEFAULT_LAYER,
      artifactId: searchParams.get('id') || undefined,
      sessionId: searchParams.get('session') || undefined,
    };
  }, [searchParams]);

  // Update URL with new state
  const updateParams = useCallback((newState: Partial<NotebookURLState>) => {
    setSearchParams((prev) => {
      const next = new URLSearchParams(prev);

      // Always set tab and layer
      const tab = newState.tab ?? state.tab;
      const layer = newState.layer ?? state.layer;

      // Only set tab if not default (cleaner URLs)
      if (tab !== DEFAULT_TAB) {
        next.set('tab', tab);
      } else {
        next.delete('tab');
      }

      // Only set layer if not default
      if (layer !== DEFAULT_LAYER) {
        next.set('layer', layer);
      } else {
        next.delete('layer');
      }

      // Handle optional params
      if ('artifactId' in newState) {
        if (newState.artifactId) {
          next.set('id', newState.artifactId);
        } else {
          next.delete('id');
        }
      }

      if ('sessionId' in newState) {
        if (newState.sessionId) {
          next.set('session', newState.sessionId);
        } else {
          next.delete('session');
        }
      }

      return next;
    }, { replace: true }); // Use replace to avoid polluting history
  }, [setSearchParams, state.tab, state.layer]);

  // Individual setters
  const setTab = useCallback((tab: NotebookTab) => {
    updateParams({ tab });
  }, [updateParams]);

  const setLayer = useCallback((layer: NotebookLayer) => {
    updateParams({ layer });
  }, [updateParams]);

  const setArtifactId = useCallback((artifactId: string | undefined) => {
    updateParams({ artifactId });
  }, [updateParams]);

  const setSessionId = useCallback((sessionId: string | undefined) => {
    updateParams({ sessionId });
  }, [updateParams]);

  // Batch update
  const setState = useCallback((newState: Partial<NotebookURLState>) => {
    updateParams(newState);
  }, [updateParams]);

  // Generate deep link for sharing
  const getDeepLink = useCallback((overrides?: Partial<NotebookURLState>): string => {
    const merged = { ...state, ...overrides };
    const params = new URLSearchParams();

    if (merged.tab !== DEFAULT_TAB) params.set('tab', merged.tab);
    if (merged.layer !== DEFAULT_LAYER) params.set('layer', merged.layer);
    if (merged.artifactId) params.set('id', merged.artifactId);
    if (merged.sessionId) params.set('session', merged.sessionId);

    const queryString = params.toString();
    return queryString ? `?${queryString}` : '';
  }, [state]);

  return {
    state,
    setTab,
    setLayer,
    setArtifactId,
    setSessionId,
    setState,
    getDeepLink,
  };
}

export default useNotebookURL;
